const TerminusClient = require('@terminusdb/terminus-client');
const TerminusHTMLViewer = require("../TerminusHTMLViewer");
const TerminusCodeSnippet = require('../viewer/TerminusCodeSnippet');
const WOQLRule = require("../viewer/WOQLRule");

function QueryPaneManager(client){
    this.client = client;
}

QueryPaneManager.prototype.queryEditor = function(){
    var woql = TerminusClient.WOQL;
	var tcs = new TerminusCodeSnippet({}, 500, 250, 'Enter Query ...', 'edit');
	var snippet = tcs.getAsDOM();
	return snippet;
}

QueryPaneManager.prototype.ruleEditor = function(){
    var woql = TerminusClient.WOQL;
	var tcs = new TerminusCodeSnippet({}, 300, 250, 'Enter Rules ...', 'edit');
	var snippet = tcs.getAsDOM();
	return snippet;
}

/*
    qObj: woql object
    woql:
*/
QueryPaneManager.prototype.processResults = function(qObj, woql, thv, results){
    let qres = new TerminusClient.WOQLResult(results, qObj);
    console.log('woql.table()', woql.table());
    var nt = thv.showResult(qres, woql.table());
    this.container.appendChild(nt);
    qres.first()
    var n = thv.showResult(qres, woql.chooser());
    this.container.appendChild(n);
    qres.first();
    var ng = thv.showResult(qres, woql.graph());
    this.container.appendChild(ng);
    qres.first();
}

// query string
QueryPaneManager.prototype.getqObjFromInput = function(query){
    var query = 'const WOQL = TerminusClient.WOQL ; return ' + 'WOQL.' + query;
    var qObj = new Function(query)();
    var qJson = qObj.json();
    var newQObj= TerminusClient.WOQL.json(JSON.parse(JSON.stringify(qJson)))
    return newQObj;
}

// rules
QueryPaneManager.prototype.showRuleEditor = function(woql){
    var snippet = this.ruleEditor();
    this.container.appendChild(snippet.dom);
    var self = this;
    snippet.actionButton.addEventListener('click', function(){
        var rObj = self.getqObjFromInput(snippet.snippetText.value);
        var n = this.self.showResult(self.qres, rObj);
        this.container.appendChild(n);
    })
}

QueryPaneManager.prototype.showConfig = function(woql){
    var cbtn = document.createElement('button');
    cbtn.appendChild(document.createTextNode('Config'));
    this.container.appendChild(cbtn);
    var self = this;
    cbtn.addEventListener('click', function(){
        self.showRuleEditor(woql);
    })
}

QueryPaneManager.prototype.submitQuery = function(snippet){
    let thv = new TerminusHTMLViewer(this.client);
    let WOQL = TerminusClient.WOQL;
    var self = this;
    snippet.actionButton.addEventListener('click', function(){
        let qObj = self.getqObjFromInput(snippet.snippetText.value);
        qObj.execute(self.client).then((results) => {
            self.processResults(qObj, WOQL, thv, results);
            self.showConfig(WOQL);
        })
    })
}

QueryPaneManager.prototype.queryPane = function(){
    var cont = document.createElement('div');
    this.container = cont;
    cont.setAttribute('style', '1px solid black');
    var snippet = this.queryEditor();
    cont.appendChild(snippet.dom);
    this.submitQuery(snippet);
    return cont;
}

QueryPaneManager.prototype.getAsDOM = function(){
    return this.queryPane();
}

module.exports = QueryPaneManager;
